"use client";

import { useParams } from "next/navigation";
import ChaptersDrawer from "../../components/ChaptersDrawer";

export default function ChaptersPage() {
  const params = useParams();
  const selectedChapter = params.id as string;

  return (
    <div className="flex">
      <ChaptersDrawer activeChapter={selectedChapter} />
      <div className="flex-1 p-8 text-white">
        <h1 className="text-3xl font-bold">Chapter: {selectedChapter}</h1>
        <p>Content of the selected chapter will go here...</p>
      </div>
    </div>
  );
}
